.checkout {
    text-align: center;
}

.checkout-btn {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0%);
    bottom: 100px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    font-size: 14px;
}

.checkout-form .checkout-btn {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0%);
    bottom: 60px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    font-size: 14px;
}

.checkout-btn:hover {
    cursor: pointer;
}

.checkout-form {
    max-width: 450px;
    margin: auto;
}

.input-left {
    text-align: left;
    margin-left: 35px;
}

.checkout-form h1 {
    text-align: center;
    font-size: 16px;
    margin: 0px auto 20px;
}

.checkout-form p {
    width: 80%;
    max-width: 450px;
    margin: 20px auto;
    text-align: left;
    font-size: 12px;
    opacity: 0.7;
}

