.checkout-btn:disabled {
    cursor: default;
    background: #f2f2f2;
    color: #c3c3c3;
    box-shadow: none;
}

.form-input {
    width: 50%;
    font-size: 12px;
    border: none;
    border-bottom: 1px solid #aaa;
    padding: 5px;
    margin: 1.5%;
    border-radius: 0px;
}

.invalid {
    border-bottom: 1px solid red;
}

.invalid:focus {
    box-shadow: 0px 0px 0px 4px red;
}

.checkbox-ctr {
    margin-top: 0px;
}

.checkbox {
    margin-right: 20px;
}

.checkbox {
    display: inline-block;
    position: relative;
    bottom: 60px;
}

.checkbox-ctr p {
    width: 65%;
    display: inline-block;
    font-size: 10px;
}

.spinner-ctr * {
    position: relative;
    width: 100%;
    margin-top: 50px;
}

.checkout-message {
    margin-top: 50px;
}

.checkout-message p {
    font-size: 18px;
    text-align: center;
}

/* Chrome, Safari, Edge, Opera */
.checkout-form input::-webkit-outer-spin-button,
.checkout-form input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.checkout-form input[type=number] {
  -moz-appearance: textfield;
}

@media (min-width: 400px) {
    .checkbox {
        bottom: 40px;
    }
}

@media (min-height: 650px) {
    .checkout-form .checkout-btn {
        bottom: 25%;
    }
}
@media (min-height: 800px) {
    .checkout-form .checkout-btn {
        bottom: 30%;
    }
}